@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  /*outline: 2px solid red;
  outline-offset: -2px;*/
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.heart {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heart-container {
  width: 460px;
  height: 407px;
  position: relative;
}

.heart-container__chat-image {
  width: 335px;
  height: 313px;
  position: absolute;
  inset: 50%;
  transform: translate(-50%, -50%);
  opacity: 60%;
  z-index: 6;
}

@keyframes heart {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    transform: translate(-50%, -50%) scale(1.2);
  }
}
.heart-container__heart-image {
  width: 197px;
  height: 175px;
  position: absolute;
  inset: 50%;
  transform: translate(-50%, -50%);
  z-index: 7;
  animation-name: heart;
  animation-duration: 1000ms;
  animation-timing-function: linear;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

@keyframes first-ellipse {
  0% {
    transform: translate(-5%, 0);
  }
  100% {
    transform: translate(14%, 5%);
  }
}
.heart-container__ellipse-image {
  fill-opacity: 50%;
}

.heart-container__ellipse-image--first {
  position: absolute;
  top: 0;
  left: 5%;
  transform: translate(-5%, 0%);
  z-index: 4;
  animation-name: first-ellipse;
  animation-duration: 4000ms;
  animation-timing-function: linear;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

@keyframes second-ellipse {
  0% {
    transform: translate(-85%, -5%);
  }
  100% {
    transform: translate(-100%, 20%);
  }
}
.heart-container__ellipse-image--second {
  position: absolute;
  top: 5%;
  left: 85%;
  transform: translate(-85%, -5%);
  z-index: 4;
  animation-name: second-ellipse;
  animation-duration: 4000ms;
  animation-timing-function: linear;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

@keyframes third-ellipse {
  0% {
    transform: translate(0%, -50%);
  }
  100% {
    transform: translate(30%, -50%);
  }
}
.heart-container__ellipse-image--third {
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translate(0%, -50%);
  z-index: 5;
  animation-name: third-ellipse;
  animation-duration: 4000ms;
  animation-timing-function: linear;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

@keyframes fourth-ellipse {
  0% {
    transform: translate(-30%, -90%);
  }
  100% {
    transform: translate(-25%, -100%);
  }
}
.heart-container__ellipse-image--fourth {
  position: absolute;
  top: 90%;
  left: 30%;
  transform: translate(-30%, -90%);
  z-index: 5;
  animation-name: fourth-ellipse;
  animation-duration: 4000ms;
  animation-timing-function: linear;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

@keyframes fifth-ellipse {
  0% {
    transform: translate(-100%, -100%);
  }
  100% {
    transform: translate(-105%, -105%);
  }
}
.heart-container__ellipse-image--fifth {
  position: absolute;
  top: 100%;
  left: 100%;
  transform: translate(-100%, -100%);
  z-index: 4;
  animation-name: fifth-ellipse;
  animation-duration: 4000ms;
  animation-timing-function: linear;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

.chameleon {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes chameleon-background {
  80% {
    background: none;
  }
  100% {
    background: url("../img/chameleon-background.png");
  }
}
.chameleon__container {
  width: 362px;
  height: 379px;
  position: relative;
  animation-name: chameleon-background;
  animation-duration: 5s;
  animation-timing-function: cubic-bezier(0.25, 1.45, 0.59, 0.31);
  animation-delay: 2s;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

@keyframes chameleon {
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.chameleon__image-chameleon {
  position: absolute;
  top: 0%;
  left: 0%;
  z-index: 1;
  animation-name: chameleon;
  animation-duration: 5s;
  animation-timing-function: ease-out;
  animation-delay: 2s;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

.chameleon__image-branch {
  position: absolute;
  top: 39%;
  left: 36%;
}

@keyframes eye {
  0% {
    transform: translate(3px, 0px);
  }
  20% {
    transform: translate(-13px, 0px);
  }
  40% {
    transform: translate(-13px, 10px);
  }
  60% {
    transform: translate(-6px, 15px);
  }
  70% {
    transform: translate(-7px, 6px);
  }
  80% {
    transform: translate(-7px, 6px) scale(1.5);
    opacity: 1;
  }
  90% {
    transform: translate(-7px, 6px) scale(0.5);
    opacity: 0;
  }
  100% {
    transform: translate(-7px, 6px) scale(0.5);
    opacity: 0;
  }
}
.chameleon__image-chameleon-eye {
  position: absolute;
  top: 49%;
  left: 45%;
  z-index: 2;
  animation-name: eye;
  animation-duration: 5s;
  animation-timing-function: ease-out;
  animation-delay: 2s;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

.cake {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes cake-background {
  0% {
    background: none;
  }
  100% {
    background: url("../img/cake-background.png");
  }
}
@keyframes cake-background-move {
  0% {
    background: url("../img/cake-background.png") no-repeat center center/50%;
  }
  100% {
    background: url("../img/cake-background.png") no-repeat center center/200%;
  }
}
.cake__container {
  width: 463px;
  height: 508px;
  position: relative;
  border-radius: 50%;
  animation-name: cake-background, cake-background-move;
  animation-duration: 5s, 3s;
  animation-timing-function: linear;
  animation-delay: 2s, 4s;
  animation-iteration-count: 1;
  animation-direction: alternate;
  animation-fill-mode: forwards;
}

.cake__image-cake {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}